h1,h2,h3,h4,h5,h6{margin-bottom:.5rem;font-weight:500;line-height:1.2}h1{font-size:2.5rem}h2{font-size:2rem}h3{font-size:1.75rem}h4{font-size:1.5rem}h5{font-size:1.25rem}h6{font-size:1rem}.button{position:relative;display:flex;justify-content:center;align-items:center;padding:.375rem .75rem;font-family:"Lato",sans-serif;font-size:1rem;line-height:1.5;white-space:nowrap;font-weight:400;border:1px solid transparent;border-radius:.25rem;box-shadow:inset 0 1px 0 rgba(255,255,255,0.15),0 1px 1px rgba(0,0,0,0.075);-webkit-transition:color 0.15s ease-in-out,background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out;transition:color 0.15s ease-in-out,background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out}.button.primary{background-color:#1f8be2;border:1px solid #1f8be2;color:#fff}.button.primary:focus{outline:0;background-color:#1870b6;border-color:#1870b6;box-shadow:0 0 0 0.2rem rgba(31,139,226,0.25)}.button.primary:hover{background-color:#1870b6;border-color:#1870b6;text-decoration:none}.button.primary:disabled{opacity:.65;box-shadow:none}.button.primary:not(:disabled):active{background-color:#125489;box-shadow:inset 0 3px 5px rgba(0,0,0,0.125)}.button.primary:not(:disabled):active:focus{outline:0;box-shadow:0 0 0 0.2rem rgba(31,139,226,0.25),inset 0 3px 5px rgba(0,0,0,0.125)}

