h1,h2,h3,h4,h5,h6{margin-bottom:.5rem;font-weight:500;line-height:1.2}h1{font-size:2.5rem}h2{font-size:2rem}h3{font-size:1.75rem}h4{font-size:1.5rem}h5{font-size:1.25rem}h6{font-size:1rem}.profile{position:relative;background-color:#fff;margin-top:-194px;height:400px;width:100%;border:none;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.35)}.profile__image{position:absolute;left:0;right:0;bottom:80%;width:150px;margin-left:auto;margin-right:auto}.profile__image img{width:150px;height:150px;border-radius:50%;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.35)}.profile__container{padding:10px}.container__infos{max-width:100%;margin-top:68px;text-align:center}.container__infos--name{position:relative;display:block;font-size:2.18rem;padding-top:10px}.container__infos--job{font-size:1.56em;text-transform:capitalize;margin-top:10px}.profile__contact{max-width:100%;font-size:1rem;margin-left:50px}.profile__contact .contact__mail,.profile__contact .contact__skype,.profile__contact .contact__phone,.profile__contact .contact__place{margin-top:10px}.contact__mail span,.contact__skype span,.contact__phone span,.contact__place span{margin-left:10px}.profile__social-media{margin-top:60px;text-align:center}.social-media__icon{display:inline;cursor:pointer}.social-media__icon:not(:first-of-type){margin-left:10px}@media (min-width: 789px){.profile{height:400px}}

